@font-face {
    font-family: 'Pretendard';
    src: url('../fonts/PretendardVariable.ttf') format('woff');
}

@font-face {
    font-family: 'ogg';
    src: url('../fonts/Ogg-Roman.otf') format('woff');
}

.bodyPart{
}



.profileBanner {
    height: 200px;
    background-color: #CCCCCC;
}

.profileBanner > .profilePic{
    display: flex;
    margin: 0 auto;
    text-align: center;
}

.profileBanner > .profilePic > img{
    display: flex;
    margin: 22px auto;
    text-align: center;

    width: 100px;
    height: 100px;
    object-fit: cover;
}

.profileLetter{
    position: relative;
    margin: -88px auto;
    text-align: center;
    font-family: "Pretendard ExtraBold";
    font-size: 35px;
}

.profileName{
    position: relative;
    margin:135px auto;
    text-align: center;
    font-family: "Pretendard ExtraBold";
    font-size: 20px;
}

.profileNickname{
    position: relative;
    margin:-130px auto;
    text-align: center;
    color: #868686;
    font-family: "Pretendard SemiBold";
    font-size: 15px;
}

.followers{
    margin-top: 15px;
    text-align: center;
    font-size: 15px;
    font-family: 'Pretendard SemiBold';

}

