/*/////////////////////////////////////////////////*/
.grid-image{
    display:grid;
    grid-template-columns:1fr 1fr 1fr 1fr;
    justify-items: center;
    flex-wrap:wrap;
    align-items:flex-start;
    margin:30px 5% 0 5% ;
}

.wishWrapper{
    width:calc(90%);
    /*width:calc(22%);*/
    margin:0 0px 30px 0px;

}

.wishlist{
    width:calc(100%);
    display: flex;
    margin:0 30px 0px 0px;
    font-size:0; /* 여백 없애는 용도 */
}

.mainImage > img{
    width:calc(100%);
    height: 203px;
    object-fit: cover;
    margin:0 0px 15px 0px;
    border-radius: 10px;
}

.subImage> img{
    width:calc(100%);
    height: 100px;
    object-fit: cover;
    margin:0 5px 3px 3px;
    border-radius: 10px;
}

.wishlistText{
    margin : 0;
    font-family: "Pretendard ExtraBold";
}

.likesComments{
    display:flex;
    font-family: "Pretendard Light";

}

.testwishlist>.mainImage>div>img{
    width:300px;
}

.testwishlist:nth-of-type(3n){
    width: 400px;
}

.wishWrapper:nth-of-type(4n) ,
.wishWrapper:last-child {
    margin:0;
    /*background-color: pink;*/
}

@media screen and (max-width:640px){ /*반응형 만들때*/
    .grid-image > .wishlist {
        width:calc(50% - 15px);
    }
}

@media screen and (max-width:480px) {
    .grid-image  > .wishlist > img:nth-of-type(2n) {
        margin-right: 0;
    }

    .grid-image  > .wishlist > img:nth-of-type(4n) {
        margin-right: 0;
    }
}


